  height: 140px;
}

header .svg-icon-seven:hover, header .svg-icon-goethe:hover {
  transform: translateY(-4px);
}

header .introduction {
  background-color: #f9f9f9;
  border-radius: 2px;
  max-width: 600px;
  margin: 1rem auto 0;
  padding: .5rem;
  font-size: 1.5rem;
  line-height: 1.5;
  display: block;
  box-shadow: 0 1px 3px #0000001a;
