  text-align: center;
  color: #fff;
  background-color: #1f1f1f;
  margin-top: auto;
  padding: 1rem;
}

footer p {
  margin: 0;
  padding-bottom: 15px;
}

.social-links a img {
  width: auto;
  height: 40px;
  margin: 0 10px;
}

.social-links img {
  filter: invert();
}

.double-link {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.double-link a {
  margin: .5rem;
}

